define('dummy/tests/unit/components/sticky', ['ember-qunit', 'dummy/tests/helpers/start-app', 'ember'], function (ember_qunit, startApp, Ember) {

  'use strict';

  var App;

  ember_qunit.moduleForComponent('sticky', 'StickyComponent', {
    setup: function setup() {
      App = startApp['default']();
    },
    teardown: function teardown() {
      Ember['default'].run(App, 'destroy');
    }
  });

  ember_qunit.test('is a div tag', function (assert) {
    assert.equal('div', this.$().prop('tagName'));
    this.subject().teardownSticky();
  });

  ember_qunit.test('has the sticky class name', function (assert) {
    assert.equal(['sticky'], this.$().prop('classNames'));
    this.subject().teardownSticky();
  });

});